/* Modal.css */
input[type=text], input[type=button] { -webkit-appearance: none; -webkit-border-radius: 0; }


.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modal {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    max-width: 240px;
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: medium;
  }
  
  .modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    margin-top: 0;
  }
  
  .modal-header h2 {
    margin: 0;
  }
  
  .modal-header button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 18px;
  }
  

  button + button {

    margin-left: 50px
    
    }

  .w-btn {
    position: relative;
    border: none;
    display: inline-block;
    padding: 10px;
    border-radius: 15px;
    font-family: "paybooc-Light", sans-serif;
    text-decoration: none;
    font-weight: 100;
    transition: 0.5s;
    width: 50px;
    height: 10px;
  }
  
  .w-btn-outline {
    position: relative;
    padding: 15px;
    border-radius: 15px;
    font-family: "paybooc-Light", sans-serif;
    text-decoration: none;
    font-weight: 400;
    transition: 0.5s;
  }
  
  .w-btn-outline2 {
    position: left;
    padding: 15px;
    border-radius: 15px;
    font-family: "paybooc-Light", sans-serif;
    text-decoration: none;
    font-weight: 600;
    transition: 0.5s;
    
  }
  .w-btn-outline2:active{
    transform: scale(0.5);
  }
  .w-btn-outline2:hover{
    background-color: #000000;
    color: #fff;
    border-color: #fff;
  }