input[type=text], input[type=button] { -webkit-appearance: none; -webkit-border-radius: 0; }


hr {

	width : 100%;

  }




.page {
	max-width: 420px;
	margin: 0 auto;
	align-items: center;
	background-color: #ffffff;
	display:flex;
	flex-direction: column;
	gap: 12px;
	position: relative;
  }
  
  .page .top-bar {
	align-items: center;
	align-self: stretch;
	background-color: #ffffff;
	display: flex;
	flex: 0 0 auto;
	flex-direction: column;
	position: relative;
	width: 100%;
	color: #000000;
	flex: 1;
	font-size: 30px;
	font-weight: 700;
	letter-spacing: 0;
	line-height: 24px;
	margin-top: -1px;
	position: relative;
  }


  
  /* .page .title {
	color: #000000;
	flex: 1;
	font-size: 30px;
	font-weight: 700;
	letter-spacing: 0;
	line-height: 24px;
	margin-top: -1px;
	position: relative;

  } */
  
  .page .icon-buttons {
	align-items: center;
	display: inline-flex;
	flex: 0 0 auto;
	gap: 8px;
	position: relative;
  }
  
  .page .icon {
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
	color: #000000;
	display: -webkit-box;
	font-family: "Roboto-Regular", Helvetica;
	font-size: 16px;
	font-weight: 400;
	height: 24px;
	letter-spacing: 0;
	line-height: 24px;
	margin-top: -1px;
	overflow: hidden;
	position: relative;
	text-align: center;
	text-overflow: ellipsis;
	white-space: nowrap;
	width: 24px;
  }
  
  .page .section-title {
	align-items: center;
	align-self: stretch;
	display: flex;
	flex: 0 0 auto;
	gap: 12px;
	padding: 16px 12px 0px;
	position: relative;
	width: 100%;
  }
  
  .page .text {
	align-items: flex-start;
	display: flex;
	flex: 1;
	flex-direction: column;
	flex-grow: 1;
	position: relative;
  }
  
  .page .text-wrapper {
	align-self: stretch;
	color: #000000;
	font-family: "Roboto-Medium", Helvetica;
	font-size: 18px;
	font-weight: 1000;
	letter-spacing: 0;
	line-height: 24px;
	margin-top: -1px;
	position: relative;
  }
  
  .page .list {
	align-items: center;
	align-self: stretch;
	display: flex;
	flex: 0 0 auto;
	flex-direction: column;
	gap: 8px;
	justify-content: center;
	padding: 0px 12px;
	position: relative;
	width: 100%;
  }
  
  .page .row {
	align-items: flex-start;
	align-self: stretch;
	display: flex;
	flex: 0 0 auto;
	gap: 8px;
	position: relative;
	width: 100%;
  }
  
  .page .card {
	align-items: center;
	border: 1px solid;
	border-color: #0000001a;
	border-radius: 6px;
	display: flex;
	flex: 1;
	flex-direction: column;
	flex-grow: 1;
	overflow: hidden;
	position: relative;
  }
  
  .page .image-container {
	align-items: flex-start;
	align-self: stretch;
	display: flex;
	height: 164px;
	position: relative;
	width: 100%;
  }
  
  .page .image {
	align-self: stretch;
	background-color: #0000000d;
	flex: 1;
	flex-grow: 1;
	position: relative;
  }
  
  .page .div {
	color: #000000;
	font-family: "Roboto-Regular", Helvetica;
	font-size: 12px;
	font-weight: 400;
	height: 16px;
	left: 16px;
	letter-spacing: 0;
	line-height: 16px;
	position: absolute;
	text-align: center;
	top: 73px;
	white-space: nowrap;
	width: 132px;
  }
  
  .page .text-content {
	align-items: flex-start;
	align-self: stretch;
	display: flex;
	flex: 0 0 auto;
	flex-direction: column;
	gap: 4px;
	padding: 8px;
	position: relative;
	width: 100%;
  }
  
  .page .title-2 {
	align-self: stretch;
	color: #000000;
	font-family: "Roboto-Regular", Helvetica;
	font-size: 15px;
	font-weight: 800;
	letter-spacing: 0;
	line-height: 16px;
	margin-top: -2px;
	position: relative;
  }
  
  .page .subtitle {
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
	align-self: stretch;
	color: #000000;
	display: -webkit-box;
	font-family: "Roboto-Medium", Helvetica;
	font-size: 12px;
	font-weight: 500;
	height: 25px;
	letter-spacing: 0;
	line-height: 24px;
	overflow: hidden;
	position: relative;
	text-overflow: ellipsis;
	white-space: nowrap;
  }
  
  .page .bottom-nav {
	align-items: center;
	align-self: center;
	background-color: #ffffff;
	box-shadow: 0px 0px 6px #0000001f;
	display: flex;
	flex: 0 0 auto;
	
	width: 100%;

	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	height: 60px;

  }
  
  .page .tab {
	align-items: center;
	display: flex;
	flex: 1;
	flex-direction: column;
	flex-grow: 1;
	padding: 4px;
	position: relative;
  }
  
  .page .icon-2 {
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
	color: #000000;
	display: -webkit-box;
	font-family: "Roboto-Regular", Helvetica;
	font-size: 20px;
	font-weight: 400;
	height: 28px;
	letter-spacing: 0;
	line-height: 28px;
	margin-top: -1px;
	overflow: hidden;
	position: relative;
	text-align: center;
	text-overflow: ellipsis;
	white-space: nowrap;
	width: 28px;
  }
  
  .page .title-3 {
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
	align-self: stretch;
	color: #000000;
	display: -webkit-box;
	font-family: "Roboto-Regular", Helvetica;
	font-size: 10px;
	font-weight: 400;
	height: 14px;
	letter-spacing: 0;
	line-height: 14px;
	overflow: hidden;
	position: relative;
	text-align: center;
	text-overflow: ellipsis;
	white-space: nowrap;
  }
